import React, { Component } from 'react';
import { Link, Route, Switch } from 'react-router-dom';

import Temperature from '../Components/Temperature';


class Home extends Component {
    render() {
      return (
        <div class="container">

            <h1 class="mt-4 mb-3">
                Temperature Calculator
            </h1>  
            
            <p>
                <Temperature />
            </p>
    
        </div>        
      );
    }
}

export default Home;


